import React from 'react';
import Img from 'gatsby-image';
import { StaticQuery, graphql } from 'gatsby';
import {
  StyledTitleBox,
  StyledImgBox,
  StyledTitle,
  StyledPath,
  StyledStroke3,
} from './StyledTitle';
const Title = () => {
  return (
    <StaticQuery
      query={graphql`
        query {
          file(relativePath: { eq: "index_pic.png" }) {
            childImageSharp {
              fluid(maxWidth: 633) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      `}
      render={data => (
        <StyledTitleBox>
          <StyledTitle>
            <h2></h2>
            <h3>
              <span></span>
              <span></span>
            </h3>
            <StyledPath />
            <StyledStroke3 />
          </StyledTitle>
          <StyledImgBox>
            <Img fluid={data.file.childImageSharp.fluid} alt="pc" />
          </StyledImgBox>
        </StyledTitleBox>
      )}
    />
  );
};

export default Title;
