import React from 'react';
import Img from 'gatsby-image';
import { StaticQuery, graphql } from 'gatsby';
import { StyledContainer, StyledTitle } from '@components';
import { StyledTeamContent, StyledTeamItem } from './StyledTeam';
const TEAM = [
  {
    name: '',
    image: '4n.png',
    role: 'co-founder',
    major: '',
  },
  {
    name: '()',
    image: 'whien.png',
    role: 'co-founder',
    major: '',
  },
  {
    name: '()',
    image: 'engine.png',
    role: 'co-founder',
    major: '',
  },
];
const TeamMember = ({ name, role, img, major }) => {
  return (
    <StyledTeamItem key={name}>
      <div>
        <Img fluid={img.childImageSharp.fluid} alt="4n" />
      </div>
      <p>
        {role} - <br />
        {major}
      </p>
      <h4>{name}</h4>
    </StyledTeamItem>
  );
};
const Team = () => {
  return (
    <StaticQuery
      query={graphql`
        query {
          allFile(filter: { sourceInstanceName: { eq: "team" } }) {
            edges {
              node {
                relativePath
                childImageSharp {
                  fluid(maxWidth: 400, maxHeight: 400) {
                    ...GatsbyImageSharpFluid
                  }
                }
              }
            }
          }
        }
      `}
      render={data => (
        <StyledContainer mt="200px" lMt="153px" mdMt="60px">
          <StyledTitle>
            <h2>Team members</h2>
            <h4></h4>
          </StyledTitle>
          <StyledTeamContent>
            {TEAM.map(({ name, image, role, major }) => {
              const img = findTargetImageFileFrom(data, image);
              return (
                <TeamMember
                  name={name}
                  role={role}
                  img={img}
                  major={major}
                  key={image}
                />
              );
            })}
          </StyledTeamContent>
        </StyledContainer>
      )}
    />
  );
};

export default Team;
const findTargetImageFileFrom = (data, targetImagePath) =>
  data.allFile.edges.find(({ node }) => node.relativePath === targetImagePath)
    .node;
