import React, { Fragment, useState } from 'react';
import { useForm } from 'react-hook-form';
import validateEmail from 'validator/lib/isEmail';
import { StyledContainer, StyledTitle } from '@components';
import {
  StyledContentBox,
  StyledContentTitle,
  StyledBackground,
  StyledContentForm,
  StyledPath,
  StyledStroke3,
  StyledFormSingle,
  StyledFormSingleBox,
  StyledFormContent,
  StyledSubmit,
  StyledSubmitBox,
} from './StyledContactUs';
const ContactUs = () => {
  const [isLoading, setIsLoading] = useState(false);
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm();

  const onSubmit = data => {
    setIsLoading(true);
    fetch('https://hex-web-backend.herokuapp.com/api/message', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data),
    })
      .then(res => {
        setIsLoading(false);
        if (res.ok) {
          alert('');
          reset();
        } else {
          alert('');
        }
      })
      .catch(err => {
        setIsLoading(false);
        console.error(err);
      });
  };
  return (
    <Fragment>
      <StyledContainer mt="200px" mdMt="28px">
        <StyledTitle>
          <h2>Contact us</h2>
          <h4></h4>
        </StyledTitle>
      </StyledContainer>
      <StyledBackground>
        <StyledContentBox>
          <StyledContentTitle>
            <StyledPath />
            <p></p>
            <p>
              
              1-2
            </p>
          </StyledContentTitle>
          <StyledContentForm onSubmit={handleSubmit(onSubmit)}>
            <StyledStroke3 />
            <StyledFormSingleBox>
              <StyledFormSingle>
                <label htmlFor="name">*</label>
                <input
                  type="text"
                  name="name"
                  id="name"
                  aria-label="name"
                  placeholder=""
                  required
                  {...register('name', { required: true })}
                />
              </StyledFormSingle>
              <StyledFormSingle>
                <label htmlFor="phone"></label>
                <input
                  aria-label="phone"
                  type="text"
                  name="phone"
                  id="phone"
                  placeholder="0912-345678"
                  {...register('phone')}
                />
              </StyledFormSingle>
            </StyledFormSingleBox>
            <StyledFormSingleBox>
              <StyledFormSingle>
                <label htmlFor="company"></label>
                <input
                  type="text"
                  name="company"
                  aria-label="company"
                  id="company"
                  placeholder=""
                  {...register('company')}
                />
              </StyledFormSingle>
              <StyledFormSingle error={errors.email}>
                <label htmlFor="email">
                  *
                  {errors.email && errors.email.type === 'validate' && (
                    <span> Email</span>
                  )}
                </label>
                <input
                  type="text"
                  name="email"
                  aria-label="email"
                  id="email"
                  placeholder="imrich@mail.com"
                  required
                  {...register('email', {
                    required: true,
                    validate: value => validateEmail(value),
                  })}
                />
              </StyledFormSingle>
            </StyledFormSingleBox>
            <StyledFormContent>
              <label htmlFor="content">*</label>
              <textarea
                rows="15"
                name="content"
                id="content"
                aria-label="content"
                placeholder=""
                {...register('content', { required: true })}
                required
              />
            </StyledFormContent>
            <StyledSubmitBox>
              <StyledSubmit type="submit" loading={isLoading}>
                
              </StyledSubmit>
            </StyledSubmitBox>
          </StyledContentForm>
        </StyledContentBox>
      </StyledBackground>
    </Fragment>
  );
};

export default ContactUs;
